<template>
  <div>
    <div class="ml-l pl-l" style="display:flex;justify-content:space-between;flex-direction:row;padding-right:15px;">
      <el-button @click="$router.back()" size="small"></el-button>
      <el-button @click="printOrder" size="small"></el-button>
    </div>
    <comp-wrap>
      <div>
        <div class="header-wrap">
          <div>
            <h3 class="my-l py-l">{{ getOrderStatus(order.status, order.deliverkind) || '' }}</h3>
            <div>
              <span class="color2">: {{ order.id }}</span>
              <span style="padding: 0 4px; margin-right: 5px;" class="member-tag">{{ { '1': '', '2': '', '3': '', '4': '' }[order.deliverkind] }}</span>
              <span style="padding: 0 4px; margin-right: 5px;" class="member-tag" v-if="order.deliverkind == 1">{{ new Date(order.delivertime * 1000).toLocaleString('zh-CN', { timeZone: 'Asia/Shanghai' }) }}</span>
              <span style="padding: 0 4px; margin-right: 5px;" class="member-tag" v-if="order.deliverkind == 1 && order.delivertime < parseInt(Date.now() / 1000) && order.status == 3"></span>
              <span style="padding: 0 4px; margin-right: 5px;" class="member-tag" v-if="order.deliverkind == 1">: {{ order.delivermobile }}</span>
              <span style="padding: 0 4px; margin-right: 5px;" class="member-tag" v-if="order.deliverkind == 2">#{{ order.daynumber }}</span>
              <span style="padding: 0 4px; margin-right: 5px;" class="member-tag" v-if="order.deliverkind == 3">#{{ order.desk }}</span>
              <span style="padding: 0 4px; margin-right: 5px;" class="member-tag" v-if="order.deliverkind == 4">#{{ order.daynumber }}</span>
              <span style="padding: 0 4px; margin-right: 5px;" class="member-tag" v-if="order.user.isvip == 2"></span>
              <span style="padding: 0 4px; margin-right: 5px;" class="member-tag">{{ order.user.nickname }}({{ order.user.mobile }})</span>
            </div>
          </div>
          <div class="header-action" v-if="order.status == 2">
            <el-button @click="updateOrder(3)" type="primary" size="small">
              {{ order.deliverkind == 2 ? '' : '' }}
            </el-button>
          </div>
          <div class="header-action" v-if="order.status == 3 && order.subscriptionid == 0">
            <el-button @click="updateOrder(2)" type="primary" size="small">
              
            </el-button>
            <div class="color3 mt-l pt-l">(152). (), .</div>
          </div>
        </div>
      </div>

      <div class="order-detail_banner" v-if="order.status === 12">
        <div class="detail-banner_text">
          <span></span>
        </div>
        <div>
          <el-button @click="updateOrder(2)" size="small" type="info"></el-button>
          <el-button @click="updateOrder(8)" size="small" type="primary"></el-button>
        </div>
      </div>

      <div class="order-detail_banner" v-if="order.status === 2">
        <div class="detail-banner_text">
          <span></span>
          <span class="detail-banner_sub-text">, </span>
        </div>
        <div>
          <el-button @click="updateOrder(8)" size="small" type="info"></el-button>
        </div>
      </div>

      <div class="address-box" v-if="order.deliverkind == 2">
        <h5 class="mb-l pb-l mt-s pt-s"></h5>
        <el-row align="middle" type="flex">
          <el-col class="color1" span="2"></el-col>
          <el-col class="color1" offset="1" span="6">{{ order.address.name }}, {{ order.address.mobile }}</el-col>
        </el-row>
        <el-row align="middle" style="margin-top: 5px;" type="flex">
          <el-col span="2">
            <el-button size="small" style="padding: 2px 10px; font-size: 10px;" type="info"></el-button>
          </el-col>
          <el-col class="color1" offset="1" span="6">{{ order.address.province }} {{ order.address.city }} {{ order.address.district }} {{ order.address.address }}</el-col>
        </el-row>
      </div>

      <comp-list :text="` ${$amount1(order.amount)}`" label="" />
      <comp-list :text="getCoupon(order.coupon)" label="" v-if="order.couponid" />
      <comp-list :text="` ${$amount1(order.originfreight)}`" label="" />
      <comp-list :key="marketing.id" :label="getMarketingLabel(marketing)" :text="getMarketing(marketing)" v-for="marketing in order.marketinglist" />
      <comp-list :text="` ${$amount1(order.actualamount + order.freight)}`" gutter label="" />
      <comp-list :gutter="!order.needinvoice" :text="order.note" label="" />
      <comp-list :text="order.giftnote" label="" />
      <comp-list :text="order.invoice.kind === 0 ? '' : ''" label="" nomb v-if="order.needinvoice == 1" />
      <comp-list :text="order.invoice.name" label="" nomb v-if="order.needinvoice == 1" />
      <comp-list :text="order.invoice.taxid" gutter label="" nomb v-if="order.needinvoice == 1" />

      <div class="g-b" style="border: 1px solid #e9e9e9; margin-left: -10px; margin-right: -10px;">
        <div class="product" :key="oproduct.id" v-for="oproduct in order.orderproductsList">
          <div class="product-info">
            <div class="product-img" @click="$router.push({ name: 'products-edit', query: { id: oproduct.productid, type: 'edit' } })">
              <comp-imger :src="oproduct.product.imagesList.length ? oproduct.product.imagesList[0] : ''" />
            </div>
            <div>
              <div class="product-name">{{ oproduct.product.name }}</div>
              <div class="product-desc">{{ oproduct.optionname && '(' + Object.values(JSON.parse(oproduct.optionname)).join(', ') + ')' }}</div>
            </div>
          </div>
          <div class="price">{{ $amount1(oproduct.amount) }} </div>
          <div class="count">{{ oproduct.quantity }} </div>
        </div>
      </div>

      <comp-list :text="new Date(order.createdat * 1000).toLocaleString('zh-CN', { timeZone: 'Asia/Shanghai' })" label="" />
      <comp-list :text="new Date(order.updatedat * 1000).toLocaleString('zh-CN', { timeZone: 'Asia/Shanghai' })" label="" />
      <comp-list :text="new Date(order.paidat * 1000).toLocaleString('zh-CN', { timeZone: 'Asia/Shanghai' })" label="" v-if="order.status != 0 && order.status != 6" />
      <comp-list :text="order.transactionid" label="" v-if="order.payway == 1" />
      <comp-list v-if="order.payway == 1" text="" label="" />
      <comp-list v-if="order.payway == 2" text="" label="" />

      <comp-list v-if="order.dadastatus" :text="{ 1: '', 2: '', 3: '', 4: '', 5: '', 7: '', 8: '', 9: '', 10: '', 100: '', 1000: '' }[order.datastatus]" label="" />
      <comp-list v-if="order.dadacancelreason" :text="order.dadacancelreason" label="" />
      <comp-list v-if="order.dadadmname" :text="order.dadadmname" label="" />
      <comp-list v-if="order.dadadmmobile" :text="order.dadadmmobile" label="" />

      <comp-list v-if="order.brokeragestatus === 0" text="" label="" />
      <comp-list v-if="order.brokeragestatus !== 0" text="" label="" />

      <comp-list v-if="order.profitsharinguserid" :text="order.profitsharinguser.nickname + '(' + order.profitsharinguser.mobile + ')'" label="" />
      <comp-list v-if="order.brokerage !== 0" :text="order.brokerage / 100" label="" />

      <comp-list v-if="order.brokeragestatus === 1 && (order.status == 0 || order.status == 1 || order.status == 2 || order.status == 3 || order.status == 4)" text="" label="" />
      <comp-list v-if="order.brokeragestatus === 2" text="" label="" />
      <comp-list v-if="order.brokeragestatus === 3" text="()" label="" />
      <comp-list v-if="order.brokeragestatus === 1 && !(order.status == 0 || order.status == 1 || order.status == 2 || order.status == 3 || order.status == 4)" text="()" label="" />
    </comp-wrap>
  </div>
</template>

<script>
/* eslint-disable */
import { Message } from 'element-ui';
import { M } from '../../../service/grpc.js';
import { rpc } from '../../../service/index.js';
import { getToken } from '../../../service/helper.js';

let token = '';

const CompWrap = {
  functional: true,
  render(h, context) {
    return (
      <div class="container">
        <div class="wrap">{context.children}</div>
      </div>
    );
  }
};

const CompList = {
  functional: true,
  props: ['label', 'text', 'gutter', 'nomb'],
  render(h, context) {
    const { label, text, gutter, nomb } = context.props;
    const arr = ['info'];

    if (gutter !== undefined) arr.push('g-b');
    if (nomb !== undefined && gutter === undefined) arr.push('nomb');

    return (
      <div class={arr.join(' ')}>
        <div class="color1 mr-l pr-l title">{label}</div>
        <div class="color1 ml-l pl-l text">{text}</div>
      </div>
    );
  }
};

const orderStatus = {
  '0': '',
  '2_1': '',
  '2_2': '',
  '2_3': '',
  '2_4': '',
  '3_1': '',
  '3_2': '',
  '3_3': '',
  '3_4': '',
  '4': '',
  '6': '',
  '8': '',
  '12': ''
};
// kind 7: 0, 2, 3, 4, 6, 8, 12
// 1  2  3  4 
const getOrderStatus = (status, deliverkind) => orderStatus[status] || orderStatus[`${status}_${deliverkind}`];

export default {
  components: {
    'comp-list': CompList,
    'comp-wrap': CompWrap
  },
  data() {
    return {
      order: {
        address: {},
        invoice: {},
        marketinglist: [],
        orderproductsList: [],
        user: {},
        coupon: {}
      }
    };
  },
  async created() {
    token = getToken();
    const { id } = this.$route.query;

    // 
    const res = await rpc(
      'StoreOrderService.GetOrder',
      M('IDWithTokenRequest', {
        token: M('Token', { token }),
        id: id
      })
    );
    // 
    let productidlist = res.orderproductsList.map(v => v.productid);
    let reslist = await Promise.all([
      rpc(
        'StoreUserService.GetUser',
        M('IDWithTokenRequest', {
          token: M('Token', { token }),
          id: res.userid
        })
      ),
      rpc(
        'StoreProductService.GetProductList',
        M('ProductListQueryRequest', {
          token: M('Token', { token }),
          idlistList: productidlist,
          sortby: 1,
          sort: 1,
          count: productidlist.length
        })
      ),
      res.marketingidsList.length
        ? rpc(
            'StoreMarketingService.GetMarketingList',
            M('MarketingListQueryRequest', {
              token: M('Token', { token }),
              kind: -1,
              idlistList: res.marketingidsList,
              sortby: 1,
              sort: 1,
              count: res.marketingidsList.length
            })
          )
        : null,
      res.couponid
        ? rpc(
            'StoreCouponService.GetCoupon',
            M('IDWithTokenRequest', {
              token: M('Token', { token }),
              id: res.couponid
            })
          )
        : null,
      res.profitsharinguserid
        ? rpc(
            'StoreUserService.GetUser',
            M('IDWithTokenRequest', {
              token: M('Token', { token }),
              id: res.profitsharinguserid
            })
          )
        : null
    ]);
    res.user = reslist[0];
    res.orderproductsList.forEach(item => {
      item.product = reslist[1].productlistList.find(v => v.id === item.productid);
    });
    res.marketinglist = reslist[2] ? reslist[2].marketinglistList : [];
    res.coupon = reslist[3];
    res.profitsharinguser = reslist[4];
    productidlist = [];
    const coupontemplateidlist = [];
    res.marketinglist.forEach(item => {
      if (item.kind !== 2) {
        return;
      }
      if (item.gifttype === 0) {
        coupontemplateidlist.push(item.giftid);
      }
      if (item.gifttype === 1) {
        productidlist.push(item.giftid);
      }
    });
    reslist = await Promise.all([
      coupontemplateidlist.length
        ? rpc(
            'StoreCouponTemplateService.GetCouponTemplateList',
            M('CouponTemplateListQueryRequest', {
              token: M('Token', { token }),
              idlistList: coupontemplateidlist,
              count: coupontemplateidlist.length
            })
          )
        : null,
      productidlist.length
        ? rpc(
            'StoreProductService.GetProductList',
            M('ProductListQueryRequest', {
              token: M('Token', { token }),
              idlistList: productidlist,
              sortby: 1,
              sort: 1,
              count: productidlist.length
            })
          )
        : null
    ]);
    res.marketinglist.forEach(item => {
      item.coupontemplate = null;
      item.product = null;
      if (item.kind !== 2) {
        return;
      }
      if (item.gifttype === 0) {
        item.coupontemplate = reslist[0] ? reslist[0].coupontemplatelistList.find(v => v.id === item.giftid) : null;
      }
      if (item.gifttype === 1) {
        item.product = reslist[1] ? reslist[1].productlistList.find(v => v.id === item.giftid) : null;
      }
    });
    this.order = res;
  },

  methods: {
    printOrder() {
      rpc(
        'StoreOrderService.PrintOrder',
        M('IDWithTokenRequest', {
          token: M('Token', { token }),
          id: this.order.id
        })
      ).then(res => {
        this.$message({
          message: '',
          type: 'success'
        });
      });
    },

    updateOrder(status) {
      const row = JSON.parse(JSON.stringify(this.order));
      row.status = status;
      rpc(
        'StoreOrderService.UpdateOrder',
        M('OrderWithTokenRequest', {
          token: M('Token', { token }),
          order: M('Order', {
            ...row,
            address: M('Address', row.address),
            invoice: M('Invoice', row.invoice),
            orderproductsList: row.orderproductsList.map(v => M('OrderProduct', v))
          })
        })
      ).then(res => {
        this.order.status = status;
        this.order.updatedat = res.updatedat;
        this.$message({
          message: '',
          type: 'success'
        });
      });
    },

    getOrderStatus,
    getCoupon(coupon) {
      return ` -${this.$amount1(coupon.amount)} (${this.$amount1(coupon.reach)}${this.$amount1(coupon.amount)})`;
    },
    getMarketing(marketing) {
      if (marketing.kind === 1) {
        return ` -${this.$amount1(marketing.reduction)} (${this.$amount1(marketing.reach)}${this.$amount1(marketing.reduction)})`;
      }
      if (marketing.kind === 2) {
        if (marketing.gifttype === 1) {
          return `${this.$amount1(marketing.reach)}: ${marketing.product.name}`;
        }
        let amountstr = '';
        if (marketing.coupontemplate.amount === 0) {
          amountstr = ` ${marketing.coupontemplate.amountbegin / 100} ~  ${marketing.coupontemplate.amountend / 100}`;
        }
        if (marketing.coupontemplate.amount === 1) {
          amountstr = ` ${marketing.coupontemplate.amount / 100}`;
        }
        return `${this.$amount1(marketing.reach)}: ${marketing.coupontemplate.name}(${amountstr})`;
      }
      return ` -${this.$amount1(this.order.originfreight)} (${this.$amount1(marketing.reach)})`;
    },
    getMarketingLabel(marketing) {
      const map = { 1: '', 2: '', 3: '' };
      return map[marketing.kind];
    }
  }
};
</script>

